import classNames from 'classnames'
import { Link } from 'react-router-dom'
import './Card.scss'

function Card(props: { link: string; children: any; time?: any; closed?: boolean, externalLink?: boolean }) {
  const { link, children, time, closed, externalLink } = props

  const classes = classNames({
    card: true,
    'card--link': link,
    'card--closed': closed,
  })

  return (
    externalLink ? 
      <a
        className={classes}
        href={link}
        rel='noopener noreferrer'
        target="_blank"
      >
        {children}

        {time && <time className="card__time">{time}</time>}
      </a> :
      <Link className={classes} to={link}>
        {children}

        {time && <time className="card__time">{time}</time>}
      </Link>
  )
}

export default Card
