import React from 'react'
import { ReactComponent as Back } from '../../assets/back.svg'
import HeaderNav from '../common/header-nav/HeaderNav'
import Card from '../common/card/Card'
import { Link } from 'react-router-dom'

export default function ticketDetails() {
  return (
    <div className="app-layout">
      <header className="app-layout__header">
        <HeaderNav>
          <Link className="header-nav-btn header-nav-btn--icon" to="/">
            <Back />
          </Link>
          <strong className="header-nav__title">Справка и поддержка</strong>
        </HeaderNav>
      </header>

      <main className="app-layout__main">
        <div className="app-card-grid">
          <Card link="/expressDocs" time="" externalLink>
            <h3 className="app-title app-title--h3">Корпоративные документы</h3>
            <p className="app-text app-text--divider">Презентации, инструкции и руководства пользователя eXpress</p>
          </Card>
          <Card link="/expressFaq" time="" externalLink>
            <h3 className="app-title app-title--h3">FAQ</h3>
            <p className="app-text app-text--divider">Ответы на часто задаваемые вопросы</p>
          </Card>
          <Card link="/expressSupport" time="" externalLink>
            <h3 className="app-title app-title--h3">Связаться с тех.поддержкой</h3>
            <p className="app-text app-text--divider">
              Наши специалисты помогут, если вы не найдет ответ на свой вопрос
            </p>
          </Card>
        </div>
      </main>
    </div>
  )
}
