import React, { useEffect } from 'react'
import { Route, Switch } from 'react-router-dom'
import '../styles/app.scss'
import Main from './pages/Main'
import Support from './pages/Support'
import Notifications from './pages/Notifications'
import Tickets from './pages/Tickets'
import TicketDetails from './pages/TicketDetails'
import TicketNew from './pages/TicketNew'
import TicketConfirmation from './pages/TicketConfirmation'
import bridge from 'smartapp-bridge/build/main/index'
import { TYPES } from '../constants'
import { useSelector, useDispatch } from 'react-redux'
import { loadNotifications, loadNotificationSuccess } from '../redux/actions/ui'
import Notifier from './common/notifier/Notifier'
import { ApplicationState, Status, StatusText, Notification } from '../types'

export default function App() {
  const [openNotifier, setOpenNotifier] = React.useState(false)

  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(loadNotifications())
    bridge.onRecieve(({ type, payload }: any) => {
      if(!payload.records.length && type !== TYPES.NOTIFICATIONS_LIST) return
      const data = payload.records[0]

      dispatch(loadNotificationSuccess(data))
      setOpenNotifier(true)
      setTimeout(() => setOpenNotifier(false), 3000)
    })
    
  }, [])

  const notifications = useSelector<ApplicationState, Array<Notification>>(state => state.ui.notifications)
  const notificationFirst = notifications[0]

  const statusPrev = Status[notificationFirst?.statusPrev]
  const statusNext = Status[notificationFirst?.statusNext]
  return (<>
    <Switch>
      <Route path="/support">
        <Support />
      </Route>
      <Route path="/notifications">
        <Notifications />
      </Route>
      <Route path="/tickets">
        <Tickets />
      </Route>
      <Route path="/ticket-details/:id">
        <TicketDetails />
      </Route>
      <Route path="/ticket-new">
        <TicketNew />
      </Route>
      <Route path="/ticket-confirmation">
        <TicketConfirmation />
      </Route>
      <Route
        path="/expressDocs"
        component={() => {
          window.location.href = 'https://express.ms/docs'
          return null
        }}
      />
      <Route
        path="/expressFaq"
        component={() => {
          window.location.href = 'https://express.ms/faq'
          return null
        }}
      />
      <Route
        path="/expressSupport"
        component={() => {
          window.location.href = 'https://express.ms/support'
          return null
        }}
      />

      <Route path="/">
        <Main />
      </Route>
    </Switch>
    {openNotifier && notifications.length && <Notifier><p>Статус обращения
          <strong> № {notificationFirst?.issueId}</strong> изменен с
          <strong> {StatusText[statusPrev]}</strong> на
          <strong> {StatusText[statusNext]}</strong></p></Notifier>}
    </>
  )
}
