import { takeEvery, all, put } from 'redux-saga/effects'
import bridge from 'smartapp-bridge/build/main/index'
import { HANDLER, TYPES, METHODS, LIMIT } from '../../constants'
import { Issue, Notification } from '../../types'
import {
  loadIssuesActionType,
  loadIssueSuccess,
  loadIssueActionType,
  addIssuesActionType,
  loadIssuesSuccess,
  addIssueSuccess,
  deleteIssueActionType,
  deleteIssueSuccess,
  editIssueActionType,
  editIssueSuccess,
  LOAD_ISSUE,
  LOAD_ISSUES,
  ADD_ISSUE,
  DELETE_ISSUE,
  EDIT_ISSUE,
  LOAD_NOTIFICATIONS,
  loadNotificationsSuccess,
  setOffset,
} from '../actions/ui'
import dayjs from 'dayjs'

export function* loadIssueSaga({ payload }: loadIssueActionType) {
  try {
    const response = yield bridge.send({
      type: TYPES.ISSUE,
      handler: HANDLER.BOTX,
      payload: {
        method: METHODS.GET,
        id: payload,
      },
    })
    
    if (response.payload.status !== 'ok') return

    const record: Issue = {
      ...response.payload.record,
      created: dayjs.unix(response.payload.record.created).format('DD.MM.YYYY, HH:mm'),
      updated: dayjs.unix(response.payload.record.updated).format('DD.MM.YYYY, HH:mm'),
      deadline: dayjs.unix(response.payload.record.deadline).format('DD.MM.YYYY, HH:mm')
    }

    yield put(loadIssueSuccess(record))
  } catch (e) {
    console.error(`loadIssueSaga error: ${e}`)
  }
}
export function* loadIssuesSaga({ payload: { offset, limit } }: loadIssuesActionType) {
  try {
    const response = yield bridge.send({
      type: TYPES.ISSUE_LIST,
      handler: HANDLER.BOTX,
      payload: {
        offset,
        limit,
      },
    })

    if (response.payload.status !== 'ok') return
    const records: Array<Issue> = response.payload.records.map((record: Issue) => {
      return {
        ...record,
        created: record.created && dayjs.unix(record.created).format('DD.MM.YYYY, HH:mm'),
        updated: record.updated && dayjs.unix(record.updated).format('DD.MM.YYYY, HH:mm'),
        deadline: record.deadline && dayjs.unix(record.deadline).format('DD.MM.YYYY, HH:mm')
      }
    }) 
    yield put(loadIssuesSuccess(records))
    yield put(setOffset((offset || 0) + LIMIT))
  } catch (e) {
    console.error(`loadIssuesSaga error: ${e}`)
  }
}

export function* addIssueSaga({ payload: { data } }: addIssuesActionType) {
  try {
    const response = yield bridge.send({
      type: TYPES.ISSUE,
      handler: HANDLER.BOTX,
      payload: {
        method: METHODS.POST,
        data,
      },
    })

    if (response.payload.status !== 'ok') return

    const record: Issue = {
      ...response.payload.record,
      created: dayjs.unix(response.payload.record.created).format('DD.MM.YYYY, HH:mm'),
      updated: dayjs.unix(response.payload.record.updated).format('DD.MM.YYYY, HH:mm'),
      deadline: dayjs.unix(response.payload.record.deadline).format('DD.MM.YYYY, HH:mm')
    }

    yield put(addIssueSuccess(record))
  } catch (e) {
    console.error(`addIssueSaga error: ${e}`)
  }
}

export function* deleteIssueSaga({ payload }: deleteIssueActionType) {
  try {
    const response = yield bridge.send({
      type: TYPES.ISSUE,
      handler: HANDLER.BOTX,
      payload: {
        method: METHODS.DELETE,
        id: payload,
      },
    })
    if (response.payload?.status !== 'ok') return
    yield put(deleteIssueSuccess(payload))
  } catch (e) {
    console.error(`deleteIssueSaga error: ${e}`)
  }
}

export function* editIssueSaga({ payload: { data } }: editIssueActionType) {
  try {
    const response = yield bridge.send({
      type: TYPES.ISSUE,
      handler: HANDLER.BOTX,
      payload: {
        method: METHODS.PATCH,
        id: data.id,
        data,
      },
    })

    if (response.payload?.status !== 'ok') return

    const record: Issue = {
      ...response.payload.record,
      created: dayjs.unix(response.payload.record.created).format('DD.MM.YYYY, HH:mm'),
      updated: dayjs.unix(response.payload.record.updated).format('DD.MM.YYYY, HH:mm'),
      deadline: dayjs.unix(response.payload.record.deadline).format('DD.MM.YYYY, HH:mm')
    }
    
    yield put(editIssueSuccess(record))
  } catch (e) {
    console.error(`editIssueSaga error: ${e}`)
  }
}

export function* loadNotificationsSaga() {
  try {
    const response = yield bridge.send({
      type: TYPES.NOTIFICATIONS_LIST,
      handler: HANDLER.BOTX,
    })

    if (response.payload?.status !== 'ok') return

    const records: Array<Notification> = response.payload.records?.map((record: Notification) => {
      return {
        ...record,
        timestamp: dayjs.unix(+record.timestamp).format('DD.MM.YYYY, HH:mm'),
      }
    })
    yield put(loadNotificationsSuccess(records))
  } catch (e) {
    console.error(`loadNotificationsSaga error: ${e}`)
  }
}

export function* rootUiSaga() {
  yield all([takeEvery(LOAD_ISSUE, loadIssueSaga)])
  yield all([takeEvery(LOAD_ISSUES, loadIssuesSaga)])
  yield all([takeEvery(ADD_ISSUE, addIssueSaga)])
  yield all([takeEvery(DELETE_ISSUE, deleteIssueSaga)])
  yield all([takeEvery(EDIT_ISSUE, editIssueSaga)])
  yield all([takeEvery(LOAD_NOTIFICATIONS, loadNotificationsSaga)])
}
