import React, { useEffect } from 'react'
import './Notifications.scss'
import { Link } from 'react-router-dom'
import Card from '../card/Card'
import { ReactComponent as IconsBell } from '../../../assets/icons-bell.svg'
import { loadNotifications } from '../../../redux/actions/ui'
import { ApplicationState, Notification, StatusText } from '../../../types'
import { useSelector, useDispatch } from 'react-redux'

function Notifications() {
  const dispatch = useDispatch()
  useEffect(() => {
    dispatch(loadNotifications())   
  }, [])
  const notifications = useSelector<ApplicationState, Array<Notification>>(state => state.ui.notifications)
  return (
    <section className="notifications">
      <header className="notifications__header">
        <h2 className="app-title app-title--h2">Уведомления</h2>
        <Link className="app-text app-text--link" to="/notifications">
          Все
        </Link>
      </header>
      {!notifications.length ? (
        <div className="notifications-empty">
          <span className="app-text app-text--desc">У вас нет недавних уведомлений</span>
          <IconsBell className="notifications-empty__icon" />
        </div>
      ) : null}
      {notifications.length ? (
        <div className="notifications__list">
          {notifications.map((notification, index) => (
            <Card key={notification.issueId + index} 
              link={`/ticket-details/${notification.issueId}`} time={notification.timestamp}>
              <p className="app-text">
                Статус обращения
                <strong> № {notification?.issueId}</strong> изменен с
                <strong> {StatusText[notification.statusPrev]}</strong> на
                <strong> {StatusText[notification.statusNext]}</strong>
              </p>
            </Card>
          ))}
        </div>
      ) : null}
    </section>
  )
}

export default Notifications
