import { createAction } from 'redux-actions'
import { Issue, Notification } from '../../types'

export const LOAD_ISSUE = 'LOAD_ISSUE'
export const LOAD_ISSUE_SUCCESS = 'LOAD_ISSUE_SUCCESS'
export const LOAD_ISSUES = 'LOAD_ISSUES'
export const LOAD_ISSUES_SUCCESS = 'LOAD_ISSUES_SUCCESS'
export const ADD_ISSUE = 'ADD_ISSUE'
export const ADD_ISSUE_SUCCESS = 'ADD_ISSUE_SUCCESS'
export const DELETE_ISSUE = 'DELETE_ISSUE'
export const DELETE_ISSUE_SUCCESS = 'DELETE_ISSUE_SUCCESS'
export const EDIT_ISSUE = 'EDIT_ISSUE'
export const EDIT_ISSUE_SUCCESS = 'EDIT_ISSUE_SUCCESS'
export const LOAD_NOTIFICATIONS = 'LOAD_NOTIFICATIONS'
export const LOAD_NOTIFICATIONS_SUCCESS = 'LOAD_NOTIFICATIONS_SUCCESS'
export const LOAD_NOTIFICATION_SUCCESS = 'LOAD_NOTIFICATION_SUCCESS'
export const SET_OFFSET = 'SET_OFFSET'

export const loadIssue = createAction(LOAD_ISSUE, ( id: string) => (id))
export const loadIssueSuccess = createAction(LOAD_ISSUE_SUCCESS, (issue: Issue) => issue)

export const loadIssues = createAction(LOAD_ISSUES, ({ offset, limit }: 
    { offset: number | null; limit?: number | null }) => ({ limit, offset }))
export const loadIssuesSuccess = createAction(LOAD_ISSUES_SUCCESS, (issues: Array<Issue>) => issues)

export const addIssue = createAction(ADD_ISSUE, (data: Issue) => ({ data }))
export const addIssueSuccess = createAction(ADD_ISSUE_SUCCESS, (issue: Issue) => issue)

export const deleteIssue = createAction(DELETE_ISSUE, (id: number) => ( id ))
export const deleteIssueSuccess = createAction(DELETE_ISSUE_SUCCESS, (id: number) => ( id ))

export const editIssue = createAction(EDIT_ISSUE, (data: Issue) => ({ data }))
export const editIssueSuccess = createAction(EDIT_ISSUE_SUCCESS, (issue: Issue) => issue)

export const loadNotificationSuccess = createAction(LOAD_NOTIFICATION_SUCCESS, (data: Notification) => (data))
export const loadNotifications = createAction(LOAD_NOTIFICATIONS)
export const loadNotificationsSuccess = createAction(LOAD_NOTIFICATIONS_SUCCESS,
    (notifications: Array<Notification>) => notifications)
export const setOffset = createAction(SET_OFFSET, (offset: number) => offset)

export type loadIssuesActionType = ReturnType<typeof loadIssues>
export type loadIssuesSuccessActionType = ReturnType<typeof loadIssuesSuccess>
export type loadIssueActionType = ReturnType<typeof loadIssue>
export type loadIssueSuccessActionType = ReturnType<typeof loadIssueSuccess>
export type addIssuesActionType = ReturnType<typeof addIssue>
export type addIssueSuccessActionType = ReturnType<typeof addIssueSuccess>
export type deleteIssueActionType = ReturnType<typeof deleteIssue>
export type deleteIssueSuccessActionType = ReturnType<typeof deleteIssueSuccess>
export type editIssueActionType = ReturnType<typeof editIssue>
export type editIssueSuccessActionType = ReturnType<typeof editIssueSuccess>
export type loadNotificationsActionType = ReturnType<typeof loadNotifications>
export type loadNotificationsSuccessType = ReturnType<typeof loadNotificationsSuccess>
export type loadNotificationSuccessType = ReturnType<typeof loadNotificationSuccess>
export type setOffsetActionType = ReturnType<typeof setOffset>
