import Dropdown from 'react-dropdown'
import TextareaAutosize from 'react-autosize-textarea'
import './FormField.scss'

function FormField(props: { type: string; placeholder?: string; value?: string; description?: string }) {
  const { type, placeholder, value, description } = props

  const options = ['Android', 'iOS', 'Web/desktop']

  return (
    <div className={`form-field form-field--${type}`}>
      {type === 'dropdown' && (
        <Dropdown
          className="form-field__field form-field__field--dropdown"
          options={options}
          placeholder={placeholder}
        />
      )}

      {type === 'text' && (
        <TextareaAutosize className="form-field__field" placeholder={placeholder} defaultValue={value} />
      )}

      {description && <div className="form-field__description" dangerouslySetInnerHTML={{ __html: description }} />}
    </div>
  )
}

export default FormField
