import React, {useEffect} from 'react'
import HeaderNav from '../common/header-nav/HeaderNav'
import { ReactComponent as Back } from '../../assets/back.svg'
import { Link } from 'react-router-dom'
import Button from '../common/button/Button'
import { useParams } from "react-router-dom"
import { useSelector, useDispatch } from 'react-redux'
import { Issue, ApplicationState, Status, StatusText } from '../../types'
import { loadIssue, editIssue } from '../../redux/actions/ui'
import Alert from '../common/alert/Alert'

interface ParamTypes {
  id: string
}

export default function TicketDetails() {
  const [openAlert, setOpenAlert] = React.useState(false)

  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(loadIssue(id))
  }, [])

  let { id } = useParams<ParamTypes>()
  const issues = useSelector<ApplicationState, Array<Issue>>(state => state.ui.issues)
  const issue = issues.find(issue => issue?.id === +id )

  const handleSetOpenAlert = () => setOpenAlert(!openAlert)

  const handleCloseIssue = () => {
    if (!issue?.id) return

    dispatch(editIssue({
     id: issue.id,
     status: Status.closed
    }))
    handleSetOpenAlert()
   }

  return (
    <div className="app-layout">
      <header className="app-layout__header">
        <HeaderNav>
          <Link className="header-nav-btn header-nav-btn--icon" to="/tickets">
            <Back />  
          </Link>
          <strong className="header-nav__title">Обращение №{issue?.id}</strong>
        </HeaderNav>
      </header>
      {issue ? <>
        <main className="app-layout__main">
          <div className="app-form">
            <div className="app-form__group">
              <h3 className="app-title app-title--h4">Статус</h3>
              <span className="app-text app-text--desc">{issue?.status && StatusText[issue?.status]}</span>
            </div>

            {issue.created && (<div className="app-form__group">
              <h3 className="app-title app-title--h4">Дата создания</h3>
              <span className="app-text app-text--desc">{issue.created}</span>
            </div>)}
            {issue.updated && (<div className="app-form__group">
              <h3 className="app-title app-title--h4">Дата изменения</h3>
              <span className="app-text app-text--desc">{issue.updated}</span>
            </div>)}

            <div className="app-form__group">
              <h3 className="app-title app-title--h4">Платформа</h3>
              <span className="app-text app-text--desc">{issue.platform}</span>
            </div>

            <div className="app-form__group">
              <h3 className="app-title app-title--h4">Описание</h3>
              <span className="app-text app-text--desc">{issue.message}</span>
            </div>

            {issue.deviceInfo && (<div className="app-form__group">
              <h3 className="app-title app-title--h4 app-form__divider-inner">Дополнительная информация</h3>
              <span className="app-text app-text--desc">{issue.deviceInfo}</span>
            </div>)}

            {issue.attachments?.length && (<div className="app-form__group app-form__group--files">
              <h3 className="app-title app-title--h4 app-form__divider-inner">Вложения</h3>
              {issue.attachments?.map(attachment => (
                <div className="app-file">
                <div className="app-file__title">{attachment.name}</div>
                <div className="app-file__size">{attachment.size}</div>
              </div>
              ))}
            </div>)}
          </div>
        </main>
       {issue.status !== Status.closed && (<footer className="app-layout__footer">
          <Button variant="primary" type="default" onClick={handleSetOpenAlert}>
            ЗАКРЫТЬ ОБРАЩЕНИЕ
          </Button>
        </footer>)}
      </>
      : null}
      {openAlert && (<Alert>
        <h2 className="app-title app-title--h4">Закрыть обращение</h2>
        <p className="app-text app-text--divider">
          Вы подтверждаете, что проблема решена/не актуальна, и хотите закрыть обращение?
        </p>
        <footer className="alert__footer">
          <Button variant="primary" type="tab" onClick={handleSetOpenAlert}>
            назад
          </Button>
          <Button variant="primary" type="default" onClick={handleCloseIssue}>
            закрыть
          </Button>
        </footer>
      </Alert>)}
    </div>
  )
}
