import './ServiceDesk.scss'
import { Link } from 'react-router-dom'
import ImgSupport from '../../../assets/sd-img-support.svg'
import ImgTickets from '../../../assets/sd-img-tickets.svg'

function ServiceDesk() {
  return (
    <section className="service-desk">
      <header className="service-desk__header">
        <h1 className="app-title app-title--h1">Service Desk</h1>
      </header>
      <div className="service-desk__container">
        <Link className="service-desk-card" to="/support">
          <h3 className="app-title app-title--h3 service-desk-card__title">Справочная информация</h3>
          <img className="service-desk-card__pic" src={ImgSupport} alt="" />
        </Link>
        <Link className="service-desk-card" to="/tickets">
          <h3 className="app-title app-title--h3 service-desk-card__title">Мои обращения</h3>
          <img className="service-desk-card__pic" src={ImgTickets} alt="" />
        </Link>
      </div>
    </section>
  )
}

export default ServiceDesk
