import React from 'react'
import { ReactComponent as Back } from '../../assets/back.svg'
import HeaderNav from '../common/header-nav/HeaderNav'
import Card from '../common/card/Card'
import { Link } from 'react-router-dom'
import { ApplicationState, Notification, StatusText } from '../../types'
import { useSelector } from 'react-redux'
import { ReactComponent as IconsBell } from '../../assets/icons-bell.svg'

export default function Notifications() {
  const notifications = useSelector<ApplicationState, Array<Notification>>(state => state.ui.notifications)

  return (
    <div className="app-layout">
      <header className="app-layout__header">
        <HeaderNav>
          <Link className="header-nav-btn header-nav-btn--icon" to="/">
            <Back />
          </Link>
          <strong className="header-nav__title">Уведомления</strong>
        </HeaderNav>
      </header>

      <main className="app-layout__main">
        <div className="app-card-grid">
          {notifications.length ? (
            notifications.map((notification, index) => (
              <Card key={notification.issueId + index} link={`/ticket-details/${notification.issueId}`}
                time={notification.timestamp}>
                <p className="app-text">
                  Статус обращения
                  <strong> № {notification?.issueId}</strong> изменен с
                  <strong> {StatusText[notification.statusPrev]}</strong> на
                  <strong> {StatusText[notification.statusNext]}</strong>
                </p>
              </Card>
            ))
          ) : (<div className="notifications-empty">
          <span className="app-text app-text--desc">У вас нет недавних уведомлений</span>
          <IconsBell className="notifications-empty__icon" />
        </div>)}
        </div>
      </main>
    </div>
  )
}
