import { RouterState } from 'connected-react-router'
export interface UiState {
  issues: Array<Issue>,
  notifications: Array<Notification>,
  offset: number,
}
export interface ApplicationState {
  ui: UiState
  router: RouterState
}

export enum Status {
  all = 'all',
  opened = 'opened',
  processing = 'processing',
  closed = 'closed',
}

export enum StatusText {
  all = 'Все',
  opened = 'Открыт',
  processing = 'В работе',
  closed = 'Закрыт',
}

export enum Platform {
  ios = 'ios',
  android = 'android',
  web = 'web',
  desktop = 'desktop',
  admin = 'admin',
  bot = 'bot',
  multiplatform = 'multiplatform',
}

export interface File {
  name: string
  size: number
}

export interface Issue {
  id?: number
  status?: Status
  created?: number
  updated?: number | null
  deadline?: number | null
  platform?: Platform
  message?: string | null
  deviceInfo?: string | null
  attachments?: Array<File> | null
}
export interface Notification {
  timestamp: string,
  statusPrev: Status,
  statusNext: Status,
  issueId: number
}
